import Vue from 'vue'
import Router from 'vue-router'
import Hello from 'components/Hello'
import Apple from 'components/apple.vue'
import  banner from  'components/banner.vue'
import  redapple from 'components/redapple.vue'
import life from 'components/life.vue'
import work from 'components/work.vue'
import  active from 'components/active.vue'
import  lyout from 'components/lyout.vue'
import  detail from 'components/personDetail.vue'
import login from 'components/login.vue'
import xiaoshuo from 'components/xiaoshuo.vue'
Vue.use(Router)

export default new Router({
  linkActiveClass: 'active',
  mode:'history',
  routes: [

    {
      path:"/apple",
      component:Apple,
      children:[{
        path: "redapple",
        component: redapple
      }]
    },

    {
      path:"/hello",
      component:Hello,
      children:[
        {
        path: "life",
        component: life
        },{
          path: "work",
          component: work
        },{
          path: "active",
          component: active
        },{
          path: "/",

          component: active
        }
      ]
    },
    {
      path:"/banner",
      component:banner
    },
    {
      path:"/",
      component:lyout
    },
    {
      path:"/detail",
      component:detail
    },
    {
      path:"/login",
      component:login
    },
    {
      path:"/xiaoshuo",
      component:xiaoshuo
    }
  ]
})



