<template>
  <div class="content-wor" ref="wrapper">


      <ul>
        <li v-for="(item,index) in obj" class="item">
          <p class="p1">{{item.title}}</p>
          <p class="p2">{{item.detail}}</p>
        </li>
      </ul>

  </div>

</template>

<script>
  import {mapState} from 'vuex'
  export default {
    computed:{
      ...mapState({
        obj:'notes'
      })
    }
  }
</script>
<style scoped>
.content-wor{
  width:100%;
  height: 100%;
  overflow: hidden;
}
.item{
  width: 90%;
  min-height: 60px;
  box-shadow: 0 0  10px #aaaaaa;
  margin: 10px auto;
  background: #ffffff;
  padding: 8px ;
}
.item .p1{
  color:#323232;
  font-size: 18px;
  line-height: 24px;
  height: 24px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-bottom: 10px;
}
.item .p1{
  color:#323232;
  font-size: 14px;
  line-height: 16px;



}
</style>



