
<template>
   <div >
     <div class="header"> <span @click="addChange"></span>误入歧途</div>
     <div class="content">
         <div class="slider">
            <img :src="list[nowIndex].src" >
            <p v-text="list[nowIndex].dec"></p>
         </div>
         <div class="tab">
           <div class="item"> <router-link to="/hello/active">动态</router-link></div>
           <div class="item"> <router-link to="/hello/life">生活</router-link></div>
           <div class="item"> <router-link to="/hello/work">工作</router-link></div>
         </div>
        <div class="detail">
          <keep-alive>
           <router-view></router-view>
          </keep-alive>
        </div>
     </div>
     <cebian :showIndex="this.showIndex" ref="cebian"></cebian>
     <div class="index" v-show="this.model" @click="addChange"></div>
  </div>
</template>
<script>
  import cebian from 'components/cbianlan.vue'
  import detail from  'components/personDetail.vue'
  export default {
    data() {
      return {
         list:[
           {src:require('../assets/001.jpg'),
            dec:"描述1描述1描述1"},
           {src:require('../assets/002.jpg'),
           dec:"描述2描述1描述1"},
           {src:require('../assets/003.jpg'),
            dec:"描述3描述1描述1"},
         ],
        nowIndex:0,
        showIndex:false,
        model:false


      }
    },
    methods:{
      runSlider(){
        setInterval(() => {
          this.nowIndex=this.nowIndex+1;
        if(this.nowIndex==3){
          this.nowIndex=0
        }
        },1500)
      },
      addChange(){
       this.showIndex=!this.showIndex;
        this.model = !this.model;
        setTimeout(()=>{this.$refs.cebian. shuaxin()},200)

      }
    },
    created(){
      this.runSlider();

    },components:{
      cebian,detail
    }
  }
</script>
<style scoped>

  .header{
    height: 48px;
    text-align: center;
    line-height: 48px;
    background:#fff ;
    box-shadow: 0px 0px  0px #fff, 0px 0px  0px #fff, 0px 0px  10px #323232, 0px 0px  0px #fff;
    position: relative;
  }
  .header span{
    display:inline-block;
    width:24px;
    height:24px;
    background: #eaeaea;
    position:absolute;
    left:12px;
    top: 12px;

  }
  .header span {
   background: url(../assets/Category.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;


  }
  .content{
    position: absolute;
    top: 48px;
    width:100%;
    bottom:0;
    overflow: hidden;
  }
  .slider{
    width:100%;
    height: 160px;
    background: #ffffff;
    position: relative;
  }
  .slider p{
    position: absolute;
    width:100%;
    height:30px;
    background: rgba(7,17,27,0.5);
    bottom: 0;
    left:0;
    color:#fff;
    line-height: 30px;
    text-align: center;
    font-size: 14px;
  }
  .slider img{

    width:100%;
    height:100%;
  }
  .index{
    position:absolute;
    top:0;
    left:0;
    width:100%;
    bottom: 0;
    background: rgba(7,17,27,0.5);
  }
  .tab{
    height: 40px;
    display: flex;
    display: -webkit-flex;
    background: #ffffff;
    border-bottom: 1px solid #eaeaea;

  }
  .tab .item{
    flex:1;
    line-height: 40px;
    text-align: center;

  }
  .tab .item a{
    text-decoration: none;
    color:#323232;
  }
  .tab .item  .active{
    color: rgb(240, 20, 20)
  }
  .detail{
    position: absolute;
    top: 201px;
    left:0;
    bottom: 0;
    overflow: hidden;
    width: 100%;
  }
</style>



