
<template>
  <transition name="fade">
  <div v-show="showIndex" class="cebian" ref="cebian">
    <div class="header" >
      <div class="tou">
        <div class="tou-left">
          <p><span @click="shuaxin"></span></p>
          <h3>成员介绍</h3>
        </div>
        <div class="tou-right"></div>
      </div>
      <div ref="memberWare" id="dddd">
        <ul>
        <li class="item" v-for="(item,index) in members" @click="skip(index)"> <span class="touxiang"><img :src="item.src" ></span><span class="toutext">{{item.name}}({{item.nickname}})</span> </li>
        </ul>
      </div>
    </div>
    </div>

  </transition>
</template>
<script>
  import BScroll from 'better-scroll'
  export default {
    props:{
      showIndex:{
        type:Boolean
      }

    },
    data(){
     return{
       members:[
         {
           name:"李开",
           nickname:"开哥",
           src:require('../assets/likai.jpg')
         },
         {
           name:"李志龙",
           nickname:"骚龙",
           src:require('../assets/lizhilong.jpg')
         },
         {
           name:"余金",
           nickname:"余狗",
           src:require('../assets/yujin.jpg')
         },
         {
           name:"熊志华",
           nickname:"熊狗",
           src:require('../assets/xiongzhihua.jpg')
         },{
           name:"王振",
           nickname:"狗头",
           src:require('../assets/wangzhen.jpg')
         },{
           name:"程炜",
           nickname:"伟哥",
           src:require('../assets/chengwei.jpg')
         },{
           name:"张发杰",
           nickname:"小蜜",
           src:require('../assets/zhangfajie.jpg')
         },{
           name:"叶先友",
           nickname:"小基友",
           src:require('../assets/yexianyou.jpg')
         },{
           name:"庞志远",
           nickname:"大基友",
           src:require('../assets/pangzhiyuan.jpg')
         },

         {
           name:"王渊超",
           nickname:"操蛋",
           src:require('../assets/wangyuanchao.jpg')
         }



       ]
     }
    },
    methods:{
      _init(){
          this.scroll = new BScroll(this.$refs.memberWare, {
            click: true
          });

      },
      shuaxin(){
        this.scroll.refresh();
      },
      skip(str){
        this.$router.push({path: '/xiaoshuo'})
      }
    },
  mounted(){
     this.$nextTick(()=>{
       this._init();
     })

    }
  }
</script>
<style scoped>
  .cebian{
    width:80%;
    position: absolute;
    top:0;
    left:0;
    bottom:0;
    z-index: 10;
    overflow: hidden;
    transform: translate3d(0,0 ,0);
  }
 .fade-enter-active,.fade-leave-active{
    transition: all 1s;
  }
  .fade-enter,.fade-leave-active{
    transform: translate3d(-100%,0 ,0);
  }
  .header{
    width: 100%;
    height: 100%;
    background: #ffffff;
  }
  .tou{
    width:100%;
    height: 180px;
    display:flex;
    display: -webkit-flex;
    display: -ms-flexbox;
    background: url(../assets/logo.jpg);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
  }
  .tou-left,.tou-right {
    flex: 1;
    z-index: 11;
  }
  .tou-left p{
    margin: 80px 0 0 15px;
  }
  .tou-left p span{
    display: inline-block;
    width:60px;
    height: 60px;
    border:1px solid #eaeaea;
    border-radius: 50%;
    background:red;
    background: url(../assets/bg.jpg);
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }
  #dddd{
    position: absolute;
    width:100%;
    top:180px;
    left:0;
    bottom: 0;
    overflow: hidden;

  }
  .tou-left h3{
    line-height: 32px;
    font-size: 16px;
    color:#000;
    font-weight:bold;
    text-indent: 10px;
  }
  .item{
    width: 100%;
    height: 48px;
    border-bottom: 1px solid #eaeaea;
    line-height: 48px;
  }
  .item .touxiang{
    display:inline-block;
    width: 36px;
    height:36px;
    border-radius: 50%;
    margin-top: 6px;
    margin-left: 12px;
   overflow: hidden;
  }
  .item .touxiang img{
    width:100%;
    height:100%;
  }
  .item .toutext{
    vertical-align: top;
    margin-left: 20px;
    font-size: 16px;
  }
</style>



