import axios from 'axios'
import {PlayList,Newsong,RecommendSongsResource} from 'api/resouce'

export  default{
  getPlayList(id){
    return axios.get(PlayList,{
      params:{
        id:id
      }
    },'')
  },
  getNewSong(){
    return axios.get(Newsong,null,'')
  },
  getRecommendSongsResource () {
    return axios.get(RecommendSongsResource);
  }
}



