import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

const store = new Vuex.Store({
  state :{
     username:'',
     password:'',
     index1:0,
     notes:[]
  },

  mutations: {
  saveName(state,msg){
    state.username = msg
    state.index1=1
  },
    savePassword(state,msg){
      state.password = msg
    },
    createNote(state,msg){
      let mssg={};
      mssg.title=msg.title;
      mssg.detail=msg.detail;
      state.notes.push(mssg)
    }
  },
  actions: {
    saveName({commit},msg){
      commit('saveName',msg)
    if(window.sessionStorage){
      window.sessionStorage.setItem("username",msg)
    }
    },
    savePassword({commit},msg){
      commit('savePassword',msg)
      if(window.sessionStorage){
        window.sessionStorage.setItem("password",msg)
      }
    },
    creatednote({commit},msg){
      commit('createNote',msg)
      console.log(msg)
    }
  },
  getters:{
    showState(state){
      return state
    }
  }

})
export default store;



