<template>
  <div >
    <div>
      <p class="p1"><input type="text" id="title" v-model="title"></p>
      <p class="p2"> <textarea id="detail" cols="30" rows="10" v-model="detail"></textarea></p>
      <p @click="ontijiao" class="btn">
        提交
      </p>
    </div>
  </div>

</template>

<script>
  import {mapState,mapActions} from 'vuex'
  export default {
   data(){
     return{
       title:"",
       detail:""
     }
   },
    methods:{
      ontijiao(){
        if(this.title !==''||this.detail !==''){
          let obj={};
          obj.title = this.title;
          obj.detail = this.detail;
          this.creatednote(obj)
          this.title=""
          this.detail=""
        }

      },
      ...mapActions([
        'creatednote'
      ])
    }
  }
</script>
<style scoped>
   .p1{
    margin: 24px 48px;
    border:1px solid #eaeaea;
    height: 32px;
    border-radius: 8px;
    overflow: hidden;
  }
  .p1 input{
    border:none;
    outline:none ;
    width:100%;
    height: 100%;
    text-indent: 10px;
  }

  .p2{
    text-align: center;
    margin: 24px 48px;
    border-radius: 8px;
  }
   .p2 textarea{
     width:100%;
     max-height: 120px;
   }
.btn{
  background:#eaeaea ;
  color: #323232;
  text-align:center;
  line-height: 32px;
  margin: 24px 48px;
  border-radius: 8px;
}
</style>



