<template>

  <div class="banner">

    <div class="bannerDetail">
      <img :src="list[0].src" >
    </div>
    <p>
      <a v-for="(item,index) in list"> {{index}}</a>
    </p>
  </div>
</template>

<script>

  export default {
data(){
  return {
    list:[
      {src:require('../assets/001.jpg')},
      {src:require('../assets/002.jpg')},
      {src:require('../assets/003.jpg')}

    ],
    nowIndex:0
  }
}
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .fade-enter-active,.fade-leave-active{
    color: red;
  }
  .fade-enter,.fade-leave-active{
    opacity:0 }
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }
  p{
    transition: all 3s;
    color:blue;
  }
</style>



