<template>
    <div>
        <div class="nav">音乐专题</div>
      <div class="recommdsang">推荐歌单</div>
      <div class="recommend" >
        <div class="recom_item" v-for="item in list1">
          <div class="round">
            <img :src="item.picUrl" alt="">
          </div>
          <p class="text">{{item.name}}</p>
        </div>
      </div>
      <div class="recommend">
        <div class="recom_item" v-for="item in list2">
          <div class="round">
            <img :src="item.picUrl" alt="">
          </div>
          <p class="text">{{item.name}}</p>
        </div>

      </div>
    </div>

</template>

<script>
import axios from 'axios'
import api from 'api/common.js'
    export default {
      data(){
        return{
          list:[],
          list1:[],
          list2:[]
        }
      },
      methods:{
        dianwo(id){
          api.getRecommendSongsResource().then((res)=>{
            this.list = res.data.result;
            for(let i=0;i<this.list.length;i++){
              if(i<3){
                this.list1.push(this.list[i])
              }else{
                this.list2.push(this.list[i])
              }
          }
        });
        }
      }


    }
</script>
<style scoped>
.nav{
  height:48px;
  line-height: 48px;
  text-align: center;
  background: #d43c33;
  color: #ffffff;
}
.recommend{
  display: flex;
  display: -webkit-flex;
  padding-bottom: 16px;
  width:100%;
}
.recom_item{
  flex:1 ;
  -webkit-box-flex: 1;

}
.round{
  padding-bottom: 100%;
  position: relative;
  margin-right: 3px;
}
.recom_item img{
 position: absolute;
  left:0;
  top:0;
  width:100%;
}
.recommend .round:last-child{
  margin-right: 0px;
}
  .text{
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    padding: 6px 2px 0 6px;
    line-height: 1.2;
    font-size: 13px;
  }
  .recommdsang{
    line-height: 32px;
    text-indent:15px;
    font-size:16px;
  }
</style>



