<template>
    <div class="loginwrapper">
        <div class="login">
          <p>
          <input type="text" name="username" placeholder="用户名" v-model="newUser.username">
          </p>
          <p>
          <input type="password" name="password" placehloder="密码" v-model="newUser.password">
          </p>
          <p style="height: 40px;">
          <a href="javascript:void(0)" class="btn" @click=" userlogin">登录</a>
            {{miao}}
          </p>
        </div>
    </div>

</template>

<script>
  import { Toast } from 'mint-ui';
  import {mapState,mapActions,mapGetters} from 'vuex'
    export default {
      data(){
        return {
          newUser: {
            username: '',
            password: ''
          },
           num:0,
           miao:60,
        }
      },
      methods: {
        userlogin(){
          if(this.newUser.name===""||this.newUser.password===""){
            Toast('用户名，密码不能为空');
            return;
          }
          console.log(this.newUser.username)
          this.saveName(this.newUser.username);
          this.savePassword(this.newUser.password)
          this.$router.push('/')
        },
        ...mapActions([
          'saveName',
          'savePassword'
        ]),

      count(){

        setInterval(function(){

          console.log(this.miao)
        },1000)
      }
      },
      computed:{
        ...mapState({
          showdetail:'username'

        }),
          ...mapGetters([
            'showState'
       ])
      },
      components:{
        Toast
       }
    }
</script>
<style scoped>
  .loginwrapper{
    width: 100%;
    height:100%;
    overflow: hidden;
  }
.login{
  width: 100%;
  margin: 120px auto 0;
}
.login p{
  margin: 24px 48px;
  border:1px solid #eaeaea;
  height: 32px;
  border-radius: 8px;
  overflow: hidden;
}
  .login p input{
    border:none;
    outline:none ;
    width:100%;
    height: 100%;
    text-indent: 10px;
  }
  .login p a{
    display: inline-block;
    width:100%;
    height:100%;
    line-height: 40px;
    font-size: 16px;
    text-align: center;
    color: #ffffff;
    text-decoration: none;
    background: #20a0ff;
  }
</style>



