   </ul>
  </div>

</template>

<script>
  import BScroll from 'better-scroll'
  export default {
    data(){
      return{
        list:[
          {src:require('../assets/likai.jpg'),title:"李开要找工作,心急如焚fafafa",date:"2017-5-25"}
          ,{src:require('../assets/wangzhen.jpg'),title:"老子终于结婚了，帅不帅",date:"2017-5-1"}
          ,{src:require('../assets/xiongzhihua.jpg'),title:"我今天结婚，来整我我啊",date:"2017-5-1"}
          ,{src:require('../assets/yexianyou.jpg'),title:"叶先友说“我在西安买房了",date:"2017-4-20"}
          ,{src:require('../assets/yujin.jpg'),title:"小侄子出生了，当叔的来送礼了",date:"2017-4-2"}
          ,{src:require('../assets/pangzhiyuan.jpg'),title:"带媳妇回家过年了",date:"2017-2-3"}
          ,{src:require('../assets/wangyuanchao.jpg'),title:"有了媳妇，忘了家",date:"2017-4-20"}
          ,{src:require('../assets/zhangfajie.jpg'),title:"万年屌丝的气质",date:"2017-4-2"}
          ,{src:require('../assets/lizhilong.jpg'),title:"李志龙他竟然",date:"2017-2-3"}
        ]
      }
