    height: 100px;
    box-shadow: 0 0  10px #aaaaaa;
    margin: 10px auto;
    background: #ffffff;
    display: flex;
    padding: 8px ;
  }
.left{
  flex: 0 0  100px;
}
.left img{
  width:100%;
  height:100%;
}
.right{
