  margin-left: 8px;
  min-width: 0;
  position: relative;
}
  .right .text1{
    font-size: 16px;
    font-weight:bold;
    line-height: 24px;
    color:#323232;
    text-indent: 8px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .right .text2{
    position: absolute;
    left: 18px;;
    bottom: 8px;
    font-size: 14px;
    line-height: 16px;
    color: #d4d4d4;
  }
</style>



